body {
  overflow-x: hidden;
}

.pagination {
  padding: 5px;
  margin: 5px;
  display: inline-block;
  justify-content: center;
}

.pagination a {
  color: black;
  float: left;
  font-size: medium;
  padding: 8px 16px;
  text-decoration: none;
}

.pagination a.active {
  background-color: #1a789a;
  color: white;
}
.center {
  text-align: center;
}
.pagination a:hover:not(.active) {
  background-color: #ddd;
}
